    export default {
        data() {
            return {
                title: "评论界面",
				commandList:[],   //评价显示列表
            }
        },
		onLoad(options) {
			this.$http.lookGoodsAllCommand({
				gid:options.gid
			}).then(res => {
				console.log("res", res);
				if(res.data != 0) {
					this.commandList = res.data
				}
			})
		}
    }
